import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { View, Panel, PanelHeader, HeaderButton, List, Cell, platform, Search, IOS, ANDROID, getClassName } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Search from '@vkontakte/icons/dist/24/search';

const osname = platform();

const top_thematics = [
    {id: 3201, name: " "},
    {id: 3273, name: " "},
    {id: 3205, name: ""},
    {id: 3282, name: "  "},
    {id: 3283, name: ""},
    {id: 3284, name: " "},
    {id: 3285, name: " "},
    {id: 3286, name: "/ "},
    {id: 3287, name: "   "},
    {id: 3288, name: "   "},
    {id: 3117, name: "  "},
    {id: 3118, name: "/  "},
    {id: 3119, name: " "},
    {id: 3120, name: "  "},
    {id: 3121, name: "    "},
    {id: 3122, name: "  "}
  ];

