class Services extends React.Component {
  constructor(props) {
		super(props);
    this.state = {
      showSearch: osname === IOS,
      search: ''
    }
    this.toggleSearch = this.toggleSearch.bind(this);

    this.onChange = this.onChange.bind(this);
	}

  toggleSearch () { this.setState({ showSearch: !this.state.showSearch }); }

  onChange (search) { this.setState({ search }); }

  get thematics () {
      const search = this.state.search.toLowerCase();
      return top_thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
  }

  render() {
    return(
      <Panel id={this.props.id}>
        <PanelHeader
            left={<HeaderButton onClick={this.props.go} data-to="navigation">{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
            right={osname === ANDROID && <HeaderButton onClick={this.toggleSearch}><Icon24Search /></HeaderButton>}>
            {this.state.showSearch ?
              <Search
                theme="header"
                value={this.state.search}
                onChange={this.onChange}
                onClose={this.toggleSearch}
              /> : ''
            }
        </PanelHeader>
        <List>
            {this.thematics.map((thematic) => (
              <Cell
                key={thematic.id}
                onClick={this.props.goSearch}
              >{thematic.name}</Cell>
            ))}
          </List>
      </Panel>
    );
  }
}


Services.propTypes = {
  id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Services;



